import React, { useRef } from 'react'
import { Navigation, Pagination, Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Link } from "react-router-dom";

import 'swiper/scss';
import 'swiper/scss/navigation';
import 'swiper/scss/pagination';

function Main() {
    const img_url = process.env.PUBLIC_URL;
    const frame = useRef(null);
    return (
        <>
            <main className="main_slider">
                <div className="slide">
                    <Swiper
                        modules={[Navigation, Pagination, Autoplay]}
                        autoplay={{
                            delay: 3000,
                            disableOnInteraction: false,
                        }}
                        pagination={{ clickable: true }}
                        navigation={true}
                        spaceBetween={0}
                        loop={true}
                        slidesPerView={1}
                        centeredSlides={true}
                        speed={300}
                        onSlideChange={(swiper) => {
                            var activeSlide = swiper.activeIndex;
                            var slides = frame.current.querySelectorAll('.swiper-slide');
                            var bgColor = slides[activeSlide].getAttribute('data-bg-color');
                            frame.current.parentElement.style.backgroundColor = bgColor;
                        }}
                        ref={frame}
                    >
                        <SwiperSlide className="panel" data-bg-color='#f9f4ff'>
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//slide1.png"} alt="   !" />
                            </div>
                        </SwiperSlide>
                        <SwiperSlide className="panel" data-bg-color='#bfd9f7'>
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//slide2.png"} alt="   " />
                            </div>
                        </SwiperSlide>
                        <SwiperSlide className="panel" data-bg-color='#efd2fc'>
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//slide3.png"} alt="    !" />
                            </div>
                        </SwiperSlide>
                        <SwiperSlide className="panel" data-bg-color='#edf3fd'>
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//slide4.jpg"} alt="     " />
                            </div>
                        </SwiperSlide>
                        <SwiperSlide className="panel" data-bg-color='#edf3fd'>
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//slide5.png"} alt="WON " />
                            </div>
                        </SwiperSlide>
                    </Swiper>



                </div>
            </main>

            <ul className="movement">
                <li>
                    <a href="#;">
                         <br />
                        
                    </a>
                </li>
                <li>
                    <a href="#;">
                        
                    </a>
                </li>
                <li>
                    <a href="#;">
                        
                    </a>
                </li>
                <li>
                    <a href="#;">
                        
                    </a>
                </li>
                <li>
                    <a href="#;">
                        
                        <span className="ArrowBtn"></span>
                    </a>
                </li>
            </ul>

            <div className="main_menu">
                <a href="#;">
                      
                </a>
                <a href="#;">
                    
                </a>
                <a href="#;">
                    / 
                </a>
            </div>

            <section className="product">
                <article className="leftBox">
                    <h2></h2>
                    <div className="box">
                        <div className="icon">
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//.png"} alt="" />
                            </div>
                            <p></p>
                        </div>
                        <div className="icon">
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//.png"} alt="" />
                            </div>
                            <p></p>
                        </div>
                        <div className="icon">
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//.png"} alt="" />
                            </div>
                            <p></p>
                        </div>
                        <div className="icon">
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//.png"} alt="" />
                            </div>
                            <p></p>
                        </div>
                        <div className="icon">
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//.png"} alt="" />
                            </div>
                            <p></p>
                        </div>
                        <div className="icon">
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//.png"} alt="" />
                            </div>
                            <p></p>
                        </div>
                        <div className="icon">
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//.png"} alt="" />
                            </div>
                            <p></p>
                        </div>
                        <div className="icon">
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//ISA.png"} alt="ISA" />
                            </div>
                            <p>ISA</p>
                        </div>
                    </div>
                </article>
                <div className="rightBox">
                    <h2></h2>
                    <div className="box">
                        <div className="icon">
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//.png"} alt="" />
                            </div>
                            <p></p>
                        </div>
                        <div className="icon">
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//.png"} alt="" />
                            </div>
                            <p></p>
                        </div>
                        <div className="icon">
                            <div className="imgBox">
                                <img src={img_url + "/img/main_page//.png"} alt="" />
                            </div>
                            <p></p>
                        </div>
                    </div>
                </div>
            </section>

            <section className="service">
                <h2></h2>
                <div className="inner">
                    <a href="#;" className="service_tab" title="WON ">
                        <div className="imgBox">
                            <img src={img_url + "/img/main_page//WON.png"} alt="WON" />
                        </div>
                        <div className="text">
                            <h3>WON</h3>
                            <p>   , <br />
                                   APP</p>
                        </div>
                    </a>
                    <Link to="/mydata" className="service_tab" title="">
                        <div className="imgBox">
                            <img src={img_url + "/img/main_page//.png"} alt="" />
                        </div>
                        <div className="text">
                            <h3></h3>
                            <p>    <br />    </p>
                        </div>
                    </Link>
                    <a href="#;" className="service_tab" title=" ">
                        <div className="imgBox">
                            <img src={img_url + "/img/main_page//.png"} alt="" />
                        </div>
                        <div className="text">
                            <h3></h3>
                            <p>   <br />  </p>
                        </div>
                    </a>
                </div>
            </section>

            <section className="news">
                <div className="inner">
                    <div className="newsBox">
                        <div className="intro">
                            <h3></h3>
                            <p></p>
                        </div>
                        <ul className="list">
                            <li><a href="#;">[] 2023   ...</a> <span>2023.10.11</span></li>
                            <li><a href="#;">[]     ...</a> <span>2023.02.08</span></li>
                            <li><a href="#;">[]     ...</a> <span>2023.10.11</span></li>
                        </ul>
                    </div>
                    <div className="eventBox">
                        <div className="intro">
                            <h3></h3>
                            <p></p>
                        </div>
                        <ul className="list">
                            <li><a href="#;">  1 !</a></li>
                            <li><a href="#;">    1 !</a></li>
                            <li><a href="#;">   !  </a></li>
                        </ul>
                    </div>
                </div>
            </section>
        </>
    )
}

export default Main