import { Link } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Navigation, Autoplay } from "swiper";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import { useRef, useState } from "react";

function Card_main() {
    const [Play, setPlay] = useState("stop")
    const frame = useRef(null);
    return (
        <section id="main">
            <Swiper
                modules={[Pagination, Navigation, Autoplay]}
                autoplay={{
                    delay: 3000,
                    disableOnInteraction: false,
                }}
                pagination={{ clickable: true }}
                navigation={true}
                spaceBetween={0}
                loop={true}
                slidesPerView={1}
                centeredSlides={true}
                speed={700}
                onSlideChange={(swiper) => {
                    var activeSlide = swiper.activeIndex;
                    var slides = frame.current.querySelectorAll('.swiper-slide');
                    var bgColor = slides[activeSlide].getAttribute('data-bg-color');
                    frame.current.style.backgroundColor = bgColor;
                }}
                ref={frame}>
                <SwiperSlide className="news1" data-bg-color="#97f7db">
                    <div className="inner">
                        <div className="box">
                            <span className="flag">NEW</span>
                            <div className="tit">ALL NEW  </div>
                            <p className="desc">
                                #EVERY1 # EVERY MILE SKYPASS
                                <br />
                                #EVERY CHECK #CHECK
                            </p>
                            <div className="btn">
                                <Link to="/" >
                                    <span></span>
                                </Link>
                            </div>
                        </div>
                        <div className="imgBox">
                            <img src={`${process.env.PUBLIC_URL}/img/sub_page//1.png`} alt="ALL NEW  " className="img" />
                        </div>
                    </div>
                </SwiperSlide>
                <SwiperSlide className="notice" data-bg-color="#dbd1fc">
                    <div className="inner">
                        <div className="box">
                            <span className="flag">NOTICE</span>
                            <div className="tit">
                                 2~6<br />
                                
                            </div>
                            <p className="desc">
                                # 2~3 # 2~6
                                <br />
                                #, 2~5
                            </p>
                            <div className="btn">
                                <Link to="/">
                                    <span></span>
                                </Link>
                            </div>
                        </div>
                        <div className="imgBox">
                            <img src={`${process.env.PUBLIC_URL}/img/sub_page//2.png`} alt=" 2~6 
                             
                            " className="img" />
                        </div>
                    </div>
                </SwiperSlide>
                <SwiperSlide className="event1" data-bg-color="#a2e8ff">
                    <div className="inner">
                        <div className="box">
                            <span className="flag">EVENT</span>
                            <div className="tit">
                                    ?<br />
                                   !
                            </div>
                            <p className="desc">   15%</p>
                            <div className="btn">
                                <Link to="/">
                                    <span></span>
                                </Link>
                            </div>
                        </div>
                        <div className="imgBox">
                            <img src={`${process.env.PUBLIC_URL}/img/sub_page//3.png`} alt="   ?   ! 
                            " className="img" />
                        </div>
                    </div>
                </SwiperSlide>
                <SwiperSlide className="event2" data-bg-color="#ffddbd">
                    <div className="inner">
                        <div className="box">
                            <span className="flag">EVENT</span>
                            <div className="tit">
                                24 <br />
                                30% !
                            </div>
                            <p className="desc">2023.08.01 ~ 2023.10.31</p>
                            <div className="btn">
                                <Link to="/">
                                    <span></span>
                                </Link>
                            </div>
                        </div>
                        <div className="imgBox">
                            <img src={`${process.env.PUBLIC_URL}/img/sub_page//4.png`} alt="24  30%  
                            " className="img" />
                        </div>
                    </div>
                </SwiperSlide>
                <SwiperSlide className="news2" data-bg-color="#fdf6d2">
                    <div className="inner">
                        <div className="box">
                            <span className="flag">NEW</span>
                            <div className="tit">
                                   
                                <br />
                                  .
                            </div>
                            <p className="desc">
                                # 2~3 # 2~6
                                <br />
                                #, 2~5
                            </p>
                            <div className="btn">
                                <Link to="/">
                                    <span></span>
                                </Link>
                            </div>
                        </div>
                        <div className="imgBox">
                            <img src={`${process.env.PUBLIC_URL}/img/sub_page//5.png`} alt=" 
                              
                            " className="img" />
                        </div>
                    </div>
                </SwiperSlide>
                <SwiperSlide className="event3" data-bg-color="#cfe1e8">
                    <div className="inner">
                        <div className="box">
                            <span className="flag">EVENT</span>
                            <div className="tit">
                                 1<br />
                                !
                            </div>
                            <p className="desc">
                                 10   
                                <br />
                                # #
                                #
                                <br />
                                # #
                            </p>
                            <div className="btn">
                                <Link to="/">
                                    <span></span>
                                </Link>
                            </div>
                        </div>
                        <div className="imgBox">
                            <img src={`${process.env.PUBLIC_URL}/img/sub_page//6.png`} alt=" 1 ! 
                            " className="img" />
                        </div>
                    </div>
                </SwiperSlide>

            </Swiper >
            <div className="swiperBtn">
                <button type="button" className={`stop ${Play === "stop" ? "on" : ""}`} onClick={() => { setPlay("play"); frame.current.swiper.autoplay.stop(); }}></button>
                <button type="button" className={`play ${Play === "play" ? "on" : ""}`} onClick={() => { setPlay("stop"); frame.current.swiper.autoplay.start(); }}></button>
            </div>
            <div div className="eventBtn" >
                <span className="txt"><span className="point"></span> </span>
            </div >
            {/* <div className="navi">
                <button className="ico list on"></button>
                <button className="ico card"></button>
            </div> */}
            <ul className="subMenu">
                <li>
                    <Link to="/card#;" className="menu">
                        <span className="imgBox">
                            <img src={`${process.env.PUBLIC_URL}/img/sub_page//ico_quick_menu1.png`} alt="" />
                        </span>
                        <span className="tit">  </span>
                    </Link>

                </li>
                <li>
                    <Link to="/card#;" className="menu">
                        <span className="imgBox">
                            <img src={`${process.env.PUBLIC_URL}/img/sub_page//ico_quick_menu2.png`} alt="" />
                        </span>
                        <span className="tit">  </span>
                    </Link>
                </li>
                <li>
                    <Link to="/card#;" className="menu">
                        <span className="imgBox">
                            <img src={`${process.env.PUBLIC_URL}/img/sub_page//ico_quick_menu3.png`} alt="" />
                        </span>
                        <span className="tit">  </span>
                    </Link>
                </li>
                <li>
                    <Link to="/card#;" className="menu">
                        <span className="imgBox">
                            <img src={`${process.env.PUBLIC_URL}/img/sub_page//ico_quick_menu4.png`} alt="" />
                        </span>
                        <span className="tit">  </span>
                    </Link>
                </li>
                <li>
                    <Link to="/card#;" className="menu">
                        <span className="imgBox">
                            <img src={`${process.env.PUBLIC_URL}/img/sub_page//ico_quick_menu5.png`} alt="" />
                        </span>
                        <span className="tit">  </span>
                    </Link>

                </li>
                <li>
                    <Link to="/card#;" className="menu">
                        <span className="imgBox">
                            <img src={`${process.env.PUBLIC_URL}/img/sub_page//ico_quick_menu6.png`} alt=" " />
                        </span>
                        <span className="tit">  </span>
                    </Link>




                </li>
            </ul >
        </section >
    )
}

export default Card_main