import React, { useEffect, useRef } from 'react'
import { Link } from 'react-router-dom/cjs/react-router-dom.min'

function Header() {
  const headerBtn = useRef()
  const headerOn = useRef()
  const closeBtn = useRef()
  // const body = document.querySelector("body")

  useEffect(() => {
    document.querySelector("body").classList.add("bodyOn")
  }, [headerOn])
  useEffect(() => {
    document.querySelector("body").classList.remove("bodyOn")
  }, [closeBtn])

  const imgUrl = process.env.PUBLIC_URL;

  return (
    <header>
      <div className="inner">
        <div className="title_Box">
          <h1>
            <Link to="/">
              <img src={imgUrl + "/img/header/h1_01.png"} alt=" " />
            </Link>
          </h1>
        </div>
        <ul id="gnb">
          <li><Link to="/personal"></Link></li>
          <li><Link to="/company"></Link></li>
          <li><Link to="/mydata"></Link></li>
          <li><Link to="/jasan"></Link></li>
          <li><Link to="/finance"></Link></li>
          <li><Link to="/card"></Link></li>
          <li><Link to=""></Link></li>
          <li className="sub_gnb"></li>
          <li>GLOBAL</li>
          <li className="header_Btn" ref={headerBtn} onClick={() => {
            headerOn.current.classList.add("header_subOn")
          }}>
            <img src={imgUrl + "/img/header/upper_search_icon_new.png"} alt="" />
          </li>
        </ul>
      </div>
      <div className="header_sub" ref={headerOn}>
        <section>
          <div className="search_box">
            <input type="text" spellCheck="false" />
            <img src={imgUrl + "/img/header/inner_search_icon.png"} alt="" />
          </div>
          <div className="header_subText">
            <h3></h3>
            <ul>
              <li><Link to="#"></Link></li>
              <span></span>
              <li><Link to="#">OTP</Link></li>
              <span></span>
              <li><Link to="#"></Link></li>
              <span></span>
              <li><Link to="#"></Link></li>
              <span></span>
              <li><Link to="#"></Link></li>
            </ul>
          </div>
          <div className="header_close_btn" ref={closeBtn} onClick={() => {
            headerOn.current.classList.remove("header_subOn")
          }}>
            <img src={imgUrl + "/img/header/search_cont_close.png"} alt="" />
          </div>
        </section>
        <span></span>
      </div>
    </header>
  )
}

export default Header