import React, { useEffect, useRef } from 'react';
import { Link } from "react-router-dom";

function Mydata() {
  const frameRef = useRef(null);

    useEffect(() => {
        if (frameRef.current) {
            const frame = frameRef.current;
            const interval = setInterval(() => { 
                frame.appendChild(frame.firstChild); 
            }, 2000);

            return () => {
                clearInterval(interval);
            }
        }
    }, []);
    
    let ImgUrl = process.env.PUBLIC_URL + '/img/sub_page//';
    return (
        <>
            <div id="myData">
                <div className="imgBox">
                    <img src={ImgUrl + "bg_main_guide.png"} alt="" />
                </div>
                <div className="myData_inner">
                    <span className="txtBox">
                        <div className="txtImgBox">
                            <img src={ImgUrl + "img_main_text_1.png"} alt="      " />
                        </div>
                        <p className="in_txt">
                             !<br/>
                                .<br/>
                             ,  .
                        </p>
                    </span>
                    <div className="myD_btnBox">
                        <Link className="myD_btn join" to="/pLogin">
                            <div className="joinImgBox"><img src={ImgUrl + "icon_join.png"} alt=" " /></div>
                            <strong></strong>
                             , !
                        </Link>
                        <Link className="myD_btn guide" to="/#">
                            <div className="guideImgBox"><img src={ImgUrl + "icon_video.png"} alt="  "/></div>
                            <strong> </strong>
                               
                        </Link>
                    </div>
                    <div className="phone">
                        <ul className="phone_frame" ref={frameRef}>
                            <li key={1}>
                                <img src={ImgUrl + "img_main_motion_3.png"} alt="  "/>
                            </li>
                            <li key={2}>
                                <img src={ImgUrl + "img_main_motion_1.png"} alt="      "/>
                            </li>
                            <li key={3}>
                                <img src={ImgUrl + "img_main_motion_2.png"} alt="  "/>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Mydata